// Dependencies
import React from 'react';
import ReactDOM from 'react-dom/client';

// App
import App from './App';

// Styles
import './index.css';
import { PageType } from './contants';

// Create root
const root = ReactDOM.createRoot(document.getElementById('root') as HTMLElement);

const customerId = document.getElementById('root')?.getAttribute('customerId') || '';
const page = document.getElementById('root')?.getAttribute('page') || '';

// Render app
root.render(<App customerId={customerId} page={page as PageType} />);
