import { FC } from 'react';
import ReactApexChart from 'react-apexcharts';
import { ApexOptions } from 'apexcharts';

export const BranchenChart : FC  = () =>{
    const chartData  = {
        series: [0.099728, 0.290507, 0.238612, 0.165351,  0.203329],
        options: {
          labels: ["Gebrauchsgter", "Technologie", "Medien", "Medizin" , "Sonstige"],
          colors:['#0461B8','#165B9D','#275681','#395066','#4A4A4A'],
          chart: {
            width: 400,
            type: 'donut',
          },
          legend: {
            show: true,
            position: "right",
            containerMargin: {
              top: 0
            },
            fontSize: '16px',
            fontFamily: 'Fira Sans',
          },
          responsive: [
            {
              breakpoint: 600,
              options: {
                legend: {
                  fontSize: "11px",
                  position: "bottom",
                  width: "100%",
                }
              }
            }
          ]
        },
    }

    return(
        <div className="pie-chart-wrapper">
          <ReactApexChart options={chartData.options as ApexOptions} series={chartData.series} type="donut" />
        </div>
    )
}
