// Export esg panel
export const ESGPanel: FC = () => {
  // States
  const [collapseStatus, setCollapseStatus] = useState([false, false, false, false]);

  // Toggle collapse handler
  const handleToggleCollapse = (index: number) => {
    collapseStatus[index] = !collapseStatus[index];
    setCollapseStatus([...collapseStatus]);
  };

  // Return esg panel
  return (
    <>
      {menuList.map((menu, index) => (
        <React.Fragment key={index}>
          <MenuItem className='_MenuItem' onClick={() => handleToggleCollapse(index)}>
            <div className='MenuItem_inner'>
            {menu}
            {collapseStatus[index] ? <KeyboardArrowUpIcon/> : <KeyboardArrowDownIcon/>}
            </div>

          </MenuItem>
          <Collapse in={collapseStatus[index]}>
            {index === 0 && (
              <>
                <List className='scroll-mobile controversial_business_areas'>
                  <ListItem className='list_heading_style' key={index}>
                    <ListItemAvatar>
                      <span></span>
                    </ListItemAvatar>
                    <ListItemText className='title'>Ausschlusskriterien</ListItemText>
                    <ListItemText sx={{ textAlign: 'right' }}>
                      <div className='Tooltip_wrapper'>
                        Kein Umsatz im Geschftsfeld
                        <div className='Tooltip'>
                          <InfoSharpIcon />
                          <div className='Tooltip-content'>Die Prozentzahlen in dieser Spalte geben den Anteil der Unternehmen im Portfolio an, die gar keinen Umsatz in dem jeweiligen Geschftsfeld erzielen.</div>
                        </div>

                      </div>
                    </ListItemText>
                    <ListItemText sx={{ textAlign: 'right' }}>
                      <div className='Tooltip_wrapper'>
                        Umsatz &lt;5%
                        <div className='Tooltip'>
                          <div className='Tooltip-content'>Die Prozentzahlen in dieser Spalte geben den Anteil der Unternehmen an, die zwischen 0% und 4,9% ihrer Umstze in dem jeweiligen Geschftsfeld erzielen. 4,9% des Umsatzes in einem Geschftsfeld, ist der maximal zulssige Anteil, der unter dem Gesichtspunkt der Verhltnismigkeit toleriert wird. Das gilt nicht fr gechtete Waffen, lsand und Fracking - in diesen Bereichen greift ein absolutes Investmentverbot.</div>
                          <InfoSharpIcon />
                        </div>

                      </div>
                    </ListItemText>
                    <ListItemText sx={{ textAlign: 'right' }}>
                      <div className='Tooltip_wrapper'>
                        Umsatz &gt;5%
                        <div className='Tooltip'>
                          <div className='Tooltip-content'>
                            Die Prozentzahlen in dieser Spalte geben den Anteil der Unternehmen an, die mehr als 5% ihrer Umstze in dem jeweiligen Geschftsfeld erzielen. Mehr als 5% ist nach unseren Kriterien nicht zulssig.
                          </div>
                          <InfoSharpIcon />
                        </div>

                      </div>
                    </ListItemText>
                  </ListItem>
                  {data1.map(({ icon, title, business_segment, conversion_lt_5, conversion_gt_5 }, index) => (
                    <ListItem key={index}>
                      <ListItemAvatar>
                        <span className={`${icon}`}></span>
                      </ListItemAvatar>
                      <ListItemText className='title'>{title}</ListItemText>
                      <ListItemText sx={{ textAlign: 'right' }}>{business_segment}</ListItemText>
                      <ListItemText sx={{ textAlign: 'right' }}>{conversion_lt_5}</ListItemText>
                      <ListItemText sx={{ textAlign: 'right' }}>{conversion_gt_5}</ListItemText>
                    </ListItem>
                  ))}
                </List>
                <Stack mt={5} spacing={5} divider={<Divider />}>
                  <Typography>Diese Werte knnen sich im Laufe der Zeit ndern.</Typography>
                  <Typography>Quelle: Eigene Berechnungen Shareholder Value Management AG. Stand der Daten: 30.09.2022</Typography>
                </Stack>
              </>
            )}
            {index === 1 && (
              <Stack mt={5} spacing={5} divider={<Divider />}>
                <ControversialLevelsChart />
                <Typography>
                  <Link target={`_blank`} href="https://www.sustainalytics.com/" underline="hover"> Sustainalytics  </Link> stuft alle kontroversen Zwischenflle, die in Verbindung mit Firmen gebracht werden, in 5 Level ein. Je hher das Level, desto bedrohlicher der Zwischenfall fr das operative Geschft des Unternehmens. Darunter fallen Betrugsvorwrfe wie Steuerhinterziehung, Korruptionsverdacht oder Verletzung der Arbeits- oder Menschenrechte. Kritische Zwischenflle werden mit Level 5 bewertet. In Level 5 Unternehmen investieren wir nicht. Bestandspositoinen, die auf Level 5 hochgestuft werden, verkaufen wir.
                  Diese Werte knnen sich im Laufe der Zeit ndern.</Typography>
                <Typography>Quelle: Eigene Berechnungen Shareholder Value Management AG. Stand der Daten: 30.09.2022</Typography>
              </Stack>
            )}
            {index === 2 && (
              <>
                <List className='pdf_table'>
                  {data2.map(({ a, b }, index) => (
                    <ListItem className='pdf_table_row' key={index}>
                      <ListItemText className='title'>
                        <Typography>{a}</Typography>
                      </ListItemText>
                      <ListItemText className='pdf'>
                        <a href={b} target="_blank">
                          <img src="https://mainfirst.tools.factsheetslive.com/bundles/mainfirsttool/images/lc_pdf_icon_DE.png" />
                        </a>
                      </ListItemText>
                    </ListItem>
                  ))}
                </List>
              </>
            )}
            {index === 3 && (
              <>
                <List>
                  {data3.map(({ a, b }, index) => (
                    <ListItem key={index}>
                      <ListItemText>{a}</ListItemText>
                      <ListItemText sx={{ textAlign: 'right' }}>
                        <a href={b} target="_blank">
                          <img src="https://mainfirst.tools.factsheetslive.com/bundles/mainfirsttool/images/lc_pdf_icon_DE.png" />
                        </a>
                      </ListItemText>
                    </ListItem>
                  ))}
                </List>
              </>
            )}
            {index === 3 && (
              <>
                <List>
                  {data3.map(({ a, b }, index) => (
                    <ListItem key={index}>
                      <ListItemText>{a}</ListItemText>
                      <ListItemText sx={{ textAlign: 'right' }}>{b}</ListItemText>
                    </ListItem>
                  ))}
                </List>
                <Stack mt={5} spacing={5} divider={<Divider />}>
                  <Typography>Diese Werte knnen sich im Laufe der Zeit ndern.</Typography>
                  <Typography>Quelle: Eigene Berechnungen Shareholder Value Management AG. Stand der Daten: 30.09.2022</Typography>
                </Stack>
              </>
            )}
            {index === 4 && (
              <Table sx={{ marginY: 10 }}>
                <TableBody>
                  {data4.map((d, index) => (
                    <TableRow key={index}>
                      <TableCell>{d}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            )}
            {index === 5 && (
              <>
                <Table>
                  <TableBody>
                    {data5.map((d, index) => (
                      <TableRow key={index}>
                        <TableCell>{d}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
                <Typography>
                   2022 Morningstar. Alle Rechte vorbehalten. Weitere detailliertere Informationen ber das Morningstar
                  Sustainability Rating*, auch ber seine Methodik, finden Sie auf: https://bit.ly/3078bQt. Aus 3038 EUR
                  Flexible Allocation - Global Fonds, Stand: 31.03.22. Basierend auf 93,73% der AUM von Unternehmen
                  und 100,00% der AUM von Staaten. Die Daten beruhen nur auf Long-Positionen.
                </Typography>
              </>
            )}
          </Collapse>
        </React.Fragment>
      ))}
    </>
  );
};
