// Dependencies
import { FC } from 'react';

// Pages
import { FactSheetPage, PerformancePage, ValuePage } from '../pages';

// Interfaces
interface IRoute {
  page: PageType;
  component: FC;
}

// Page
export type PageType = 'performance' | 'value' | 'factsheet'

// Export main routes
export const MAIN_ROUTES: IRoute[] = [
  {
    page: 'performance',
    component: PerformancePage,
  },
  {
    page: 'value',
    component: ValuePage,
  },
  {
    page: 'factsheet',
    component: FactSheetPage,
  },
];
